import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { ModalController } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';

import { AlertController } from 'ionic-angular';
/**
 * Generated class for the MapaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */



@IonicPage()
@Component({
  selector: 'page-mapa',
  templateUrl: 'mapa.html',
})
export class MapaPage {

map: GoogleMap;



myPosition: any = {};
markers: any[] = [
  {
    position:{
      latitude: -17.3666745,
      longitude: -66.2387878,
    },
    title:'Point 1'
  },
  {
    position:{
      latitude: -17.3706884,
      longitude: -66.2397749,
    },
    title:'Point 2'
  },
  {
    position:{
      latitude: -17.391398,
      longitude: -66.2407904,
    },
    title:'Point 3'
  },
  {
    position:{
      latitude: -17.3878887,
      longitude: -66.223664,
    },
    title:'Point 4'
  },
];


  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController, public loadingCtrl: LoadingController,public events: Events, public apiProvider: ApiProvider, public alertCtrl: AlertController) {
  }

  ionViewDidLoad() {

  this.getServiciosGPS();
    console.log('ionViewDidLoad MapaPage');


  }



loadMap(){
  // create a new map by passing HTMLElement
  let element: HTMLElement = document.getElementById('map');

  this.map = plugin.google.maps.Map.getMap(element);

  // create CameraPosition
  let position: CameraPosition = {
    target: new LatLng(this.myPosition.latitude, this.myPosition.longitude),
    zoom: 12,
    tilt: 30
  };

  this.map.one(GoogleMapsEvent.MAP_READY).then(()=>{
    console.log('Map is ready!');

    // move the map's camera to position
    this.map.moveCamera(position);
  });
}


        getServiciosGPS(){
      
          //this.latitudePerson = 9.9931605;
          //this.longitudePerson = -84.2307427;
 

    let loading = this.loadingCtrl.create({content : "Obteniendo ubicacion"});
    loading.present();

      console.log('gps');
        navigator.geolocation.getCurrentPosition((pos) => {

          console.log(pos.coords.latitude+' Long: '+ pos.coords.longitude);

			this.myPosition = {
			latitude: pos.coords.latitude,
			longitude: pos.coords.longitude
			}
			this.loadMap();

          loading.dismissAll();
     


        }, function(error) {
          console.log('some err');
          console.log(error);
          loading.dismissAll();
        },{ enableHighAccuracy: true, timeout: 30000 });

    
      }



}



